import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { MaterializeAction } from 'angular2-materialize/src';

import { ToolsService } from '../../shared/tools.service';

import { Specialist } from '../specialist';
import { SpecialistsService } from '../specialists.service';

@Component({
  selector: 'app-specialists',
  templateUrl: './specialists.component.html',
  styleUrls: ['./specialists.component.css']
})
export class SpecialistsComponent implements OnInit {
  public specialists$: Observable<Specialist[]>;

  constructor(
    private _tools: ToolsService,
    private _router: Router,
    private _specialistsService: SpecialistsService
  ) {
    this._tools.init();

    this._tools.getToolbar()
      .change({
        title: "Список специалистов",
        name: "specialists",
        actions: {
          add: () => this._router.navigate(['specialists/add'])
        },
        actionsPosition: {
          extend: ['add']
        }
      });
  }

  ngOnInit() {
    this.reload();
  }

  deleteItem(item) {
    let specialistDelete$ = this._specialistsService.remove(item._id);

    specialistDelete$.subscribe(
      result => {
        if(result.status == 'ok') {
          this.reload();
        }
      },
      error => {
        this._tools.getToast().toast("Произошла ошибка");
      }
    );
  }

  goEditPage(item) {
    this._router.navigate(['/specialists/update/'+item._id]);
  }

  reload() {
    this.specialists$ = this._specialistsService.get().share();

    this.specialists$.subscribe();
  }

}



