import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { MaterializeModule } from "angular2-materialize/src";
import { SharedModule } from '../../shared/shared.module';

import { SpecialistsListComponent } from './specialists-list/specialists-list.component';
import { SpecialistsFormComponent } from './specialists-form/specialists-form.component';

import { SpecialistsService } from '../specialists.service';
import { UsersWidgetModule } from '../../+users/_widgets/users.widget';
import { PersonsWidgetModule } from '../../+persons/_widgets/persons.widget';

@NgModule({
  imports: [
    CommonModule,
    ReactiveFormsModule, FormsModule,
    MaterializeModule,
    UsersWidgetModule,
    PersonsWidgetModule,
    SharedModule
  ],
  providers: [SpecialistsService],
  declarations: [SpecialistsListComponent, SpecialistsFormComponent],
  exports: [SpecialistsListComponent, SpecialistsFormComponent]
})
export class SpecialistWidgetModule {  }


