import { Component, OnInit, EventEmitter, ViewChild, AfterViewInit } from '@angular/core';
import { FormControl, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router, ActivatedRoute, Params } from '@angular/router';

import { SpecialistsService } from '../specialists.service';
import { PersonsService } from '../../+persons/persons.service';

import { ToolsService } from '../../shared/tools.service';
import { FormDataService } from '../../shared/form-data.service';
import { FileSelectService } from '../../shared/file-select.service';
 
import { Specialist, ISpecialist } from '../specialist';
import { Person } from '../../+persons/person';

import { SpecialistsFormComponent } from '../_widgets/specialists-form/specialists-form.component';

@Component({
  selector: 'app-specialist-update',
  templateUrl: './specialist-update.component.html',
  styleUrls: ['./specialist-update.component.css'],
  providers: [PersonsService]
})
export class SpecialistUpdateComponent implements OnInit, AfterViewInit {
  @ViewChild(SpecialistsFormComponent) formCmp: SpecialistsFormComponent;
  public specialist: Specialist = new Specialist();
  public coincidencePersons: Person[];
  public downloaded: boolean = false;

  constructor(
    private _router: Router,
    private _route: ActivatedRoute,
    private _tools: ToolsService,
    private _personsService : PersonsService,
    private _specialistsService: SpecialistsService
  ) { }
  
  ngOnInit() {
    this._tools.getToolbar().change({
      title: "Редактировать специалиста"
    });
  
    this._tools.getHelpPanel().setAnchor('search', {
      count: 0
    });
  }

  ngAfterViewInit() {
    if(!this.downloaded) {
      this.getspecialist();
    }
  }

  setPerson(item) {
    this.specialist.setParams({person: item, active: true});
    this.formCmp.patchValue(this.specialist);
    this.formCmp.form.get('person').disable();
  }

  sendForm(values) {
    let specialistUpdate$ = this._specialistsService.update(values);
    
    specialistUpdate$.subscribe(
      specialist => this._tools.getToast().toast("Врач успешно обновлен"),
      error => this._tools.getToast().toast("Произошла ошибка")
    );
  }

  getspecialist() {
    this._route.params.forEach((params: Params) => {
			let id = params['id'];
			let specialist$ = this._specialistsService.getOne(id);

      specialist$.subscribe(specialist => {
        this.specialist.setParams(specialist);
        this.formCmp.patchValue(this.specialist);
      });
		});
  }

  returnToList() {
    this._router.navigate(['specialists']);
  }

  searchPersons(params) {
    if(params.last_name || params.first_name || params.phone || params.email) {
      this._personsService.get(params)
        .subscribe((response) => {
          this.coincidencePersons = response.items;
          this._tools.getHelpPanel().getAnchor('search').count = response.count;
        });
    }
  }

  blockLoad() {
    this.downloaded = true;
  }

}



