import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Specialist } from '../../specialist';

@Component({
  selector: 'app-specialists-list',
  templateUrl: './specialists-list.component.html',
  styleUrls: ['./specialists-list.component.css']
})
export class SpecialistsListComponent implements OnInit {

  @Input('specialists')
    specialists: Specialist[];
  
  @Output('delete')
    eDelete = new EventEmitter<Specialist>();
  @Output('edit')
    eEdit = new EventEmitter<Specialist>();

  public currentItem: Specialist;
 
  constructor() { }

  ngOnInit() {
    
  }

  editItem(item: Specialist) {
    this.currentItem = item;
    this.eEdit.emit(item);
  }

  deleteItem(item: Specialist) {
    this.currentItem = item;
    this.eDelete.emit(item);
  }
}



