import { Person, IPerson } from '../+persons/person';

export interface ISpecialist {
    tags: string[];
	experience: number;
	comments: any[];
	certificates: any[];
	achievements: string[];
	doctorate: string;
	locations: string[];
    createdAt: Date;
    updatedAt: Date;
    active: boolean;
    personId: string;
}
export class Specialist implements ISpecialist {
    _id?: string;
    tags: string[];
    tag_names: string[];
	experience: number;
	comments: any[];
	certificates: any[];
	achievements: string[];
	doctorate: string;
	locations: string[];
    createdAt: Date;
    updatedAt: Date;
    active: boolean;
    personId: string;
    person: Person;

    constructor(options?) {
        this.default();

        if(options) {
            this.setParams(options);
        }
    }

    setParams(options: {
        _id?: string,
        tags?: string[],
        tag_names?: string[],
        experience?: number,
        comments?: any[],
        certificates?: any[],
        achievements?: string[],
        doctorate?: string,
        locations?: string[],
        createdAt?: Date,
        updatedAt?: Date,
        active?: boolean,
        personId?: string
        person?: any
    }) {
        this._id = options._id || null;
        this.tags = options.tags || [];
        this.tag_names = options.tag_names || [];
        this.experience = options.experience || null;
        this.comments = options.comments || [];
        this.certificates = options.certificates || [];
        this.achievements = options.achievements || [];
        this.doctorate = options.doctorate || null;
        this.locations = options.locations || [];
        this.setDate(this.createdAt, options.createdAt);
        this.setDate(this.updatedAt, options.updatedAt);
        this.active = !!options.active;
        this.personId = options.personId || null;
        if(options.person) {
            this.personId = options.person._id || this.personId;
            this.person.setParams(options.person);
        }
    }

    private setDate(self, date) {
        if(date) {
            if(date.search(/\d{2}.\d{2}.\d{4}/i) == -1) {
                self = date;
            } else {
                let [day, month, year] = date.split('.');
                self = new Date(year, month - 1, day, 0, 0, 0);
            }
        }
    }

    default() {
        this._id = null;
        this.tags = [];
        this.experience = null;
        this.comments = [];
        this.certificates = [];
        this.achievements = [];
        this.doctorate = null;
        this.locations = [];
        this.createdAt = null;
        this.updatedAt = null;
        this.active = true;        
        this.personId = null;
        this.person = new Person();
    }
}



