import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { MaterializeModule } from "angular2-materialize/src";
import { SharedModule } from '../shared/shared.module';

import { SpecialistWidgetModule } from './_widgets/specialists.widget';

import { SpecialistsComponent } from './specialists/specialists.component';
import { SpecialistAddComponent } from './specialist-add/specialist-add.component';
import { SpecialistUpdateComponent } from './specialist-update/specialist-update.component';

import { SpecialistsService } from './specialists.service';

const SPECIALISTS_ROUTES: Routes = [
  { path: '', component: SpecialistsComponent },
  { path: 'add', component: SpecialistAddComponent },
  { path: 'update/:id', component: SpecialistUpdateComponent }
];
@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(SPECIALISTS_ROUTES),
    ReactiveFormsModule, FormsModule,
    MaterializeModule,
    SharedModule,
    SpecialistWidgetModule
  ],
  //providers: [specialistsService],
  declarations: [SpecialistsComponent, SpecialistAddComponent, SpecialistUpdateComponent],
  exports: [SpecialistsComponent]
})
export class SpecialistsModule { }



